<template>
  <section class="search">
    <button class="search__button" @click="inputPrintHandler">
      <svg viewBox="0 0 511.999 511.999" fill="#9898b5">
      <path d="M508.874,478.708L360.142,329.976c28.21-34.827,45.191-79.103,45.191-127.309c0-111.75-90.917-202.667-202.667-202.667
      S0,90.917,0,202.667s90.917,202.667,202.667,202.667c48.206,0,92.482-16.982,127.309-45.191l148.732,148.732
      c4.167,4.165,10.919,4.165,15.086,0l15.081-15.082C513.04,489.627,513.04,482.873,508.874,478.708z M202.667,362.667
      c-88.229,0-160-71.771-160-160s71.771-160,160-160s160,71.771,160,160S290.896,362.667,202.667,362.667z"/>
      </svg>
    </button>
    <input type="text" class="search__field" :class="{'search__field--active': isSearchOpened}" placeholder="Введите ваш ник-нейм" v-model="playerNikName" @keyup.enter="inputPrintHandler">
  </section>
</template>

<script>
export default {
  name: 'AppSearch',
  data() {
    return {
      playerNikName: '',
      playerAccountId: 0,
      playerProfileData: {},
      isSearchOpened: true,
    }
  },
  methods: {
    inputPrintHandler() {
      fetch(`https://api.worldoftanks.ru/wot/account/list/?application_id=08cd61e7aed554c9eac75f38a1c7cedb&search=${this.playerNikName}`)
        .then(response => response.json())
        .then(data => {
          const ACCOUNT_ID = data.data[0].account_id;

          fetch(`https://api.worldoftanks.ru/wot/account/info/?application_id=08cd61e7aed554c9eac75f38a1c7cedb&account_id=${ACCOUNT_ID}&extra=statistics.random`)
            .then(response => response.json())
            .then(data => {
              this.$store.commit("SET_PLAYER_DATA", data.data[ACCOUNT_ID]);
            }); 
          }
        );
    },
  }
}
</script>

