<template>
  <section class="profile">
    <table class="profile__table">
      <tr class="profile__row">
        <td class="profile__cell">
          <h3 class="profile__heading">Бои</h3> 
          <p class="profile__data" :style="statisticsColors.battlesColor">{{battles}}</p> 
        </td>  
        <td class="profile__cell">
          <h3 class="profile__heading">Рейтинг WN8</h3> 
          <p class="profile__data">Скоро</p> 
        </td>  
        <td class="profile__cell">
          <h3 class="profile__heading">Победы</h3> 
          <p class="profile__data" :style="statisticsColors.victoriesColor">{{victories}}%</p>
        </td>  
      </tr>
      <tr class="profile__row">
        <td class="profile__cell">
          <h3 class="profile__heading">Ср.урон</h3> 
          <p class="profile__data">{{ this.$store.getters.PLAYER_DATA ? Math.floor(this.$store.getters.PLAYER_DATA.statistics.random.damage_dealt / this.$store.getters.PLAYER_DATA.statistics.random.battles) : '-/-'}}</p>
        </td> 
        <td class="profile__cell">
          <i class="profile__icon"></i>
          <p class="profile__data" :style="statisticsColors.WOTRatingColor">{{WOTRating}}</p>
        </td> 
        <td class="profile__cell">
          <h3 class="profile__heading">Ср.опыт</h3> 
          <p class="profile__data">{{ this.$store.getters.PLAYER_DATA ? this.$store.getters.PLAYER_DATA.statistics.all.battle_avg_xp : '-/-'}}</p>
        </td>   
      </tr>
      <tr class="profile__row">
        <td class="profile__cell">
          <h3 class="profile__heading">Зарегистрирован</h3> 
          <p class="profile__time">{{("" + (new Date(this.$store.getters.PLAYER_DATA.created_at * 1000)).toISOString()).replace(/^([^T]+)T(.+)$/,'$1').replace(/^(\d+)-(\d+)-(\d+)$/,'$3.$2.$1')}}</p>
        </td> 
        <td class="profile__cell"></td> 
        <td class="profile__cell">
          <h3 class="profile__heading">Последний бой</h3> 
          <p class="profile__time">{{("" + (new Date(this.$store.getters.PLAYER_DATA.last_battle_time * 1000)).toLocaleString()).replace(/^([^T]+)T(.+)$/,'$1')}}</p>
        </td>   
      </tr>    
    </table>  
  </section> 
</template>

<script>
export default {
  name: 'AppProfile',
  data() {
    return {
      statisticsColors: {
        battlesColor: {
          color: '#FDF109'
        },
        victoriesColor: {
          color: '#5BFF00'
        },
        WOTRatingColor: {
          color: '#5BFF00'
        }
      }
    }
  },
  computed: {
    battles() {
      if (this.$store.getters.PLAYER_DATA) {
        const value = this.$store.getters.PLAYER_DATA.statistics.all.battles.toString().split('');

        if (value.length === 4) {
          return value[0] + ' ' + value.slice(1).join('');
        } else if (value.length > 4) {
          return value.slice(0, 2).join('') + ' ' + value.slice(2).join('');
        }

        return this.$store.getters.PLAYER_DATA.statistics.all.battles;
      }

        return 0;
    },
    victories() {
      return this.$store.getters.PLAYER_DATA ? ((this.$store.getters.PLAYER_DATA.statistics.all.wins / this.$store.getters.PLAYER_DATA.statistics.all.battles) * 100).toFixed(2) : 0;
    },
    WOTRating() {
      if (this.$store.getters.PLAYER_DATA) {
        const value = this.$store.getters.PLAYER_DATA.global_rating.toString().split('');

        if (value.length === 4) {
          return value[0] + ' ' + value.slice(1).join('');
        } else if (value.length > 4) {
          return value.slice(0, 2).join('') + ' ' + value.slice(2).join('');
        }

        return this.$store.getters.PLAYER_DATA.global_rating;
      }

        return 0;
    }
  },
  watch: {
    battles() {
      const value = this.battles.split(' ').join('');

      if (value < 5000) {
        this.statisticsColors.battlesCount = {
          color: '#FE0E01'
        };
      } else if (value >= 5000 && value <= 10000) {
        this.statisticsColors.battlesColor = {
          color: '#FF7D00'
        };
      } else if (value >= 10000 && value <= 20000) {
        this.statisticsColors.battlesColor = {
          color: '#FDF109'
        };
      } else if (value >= 20000 && value <= 40000) {
        this.statisticsColors.battlesColor = {
          color: '#5BFF00'
        };
      } else if (value >= 40000 && value <= 60000) {
        this.statisticsColors.battlesColor = {
          color: '#00CAB3'
        };
      } else {
        this.statisticsColors.battlesColor = {
          color: '#D93DF9'
        };
      } 
    },
    WOTRating() {
      const value = this.WOTRating.split(' ').join('');

      if (value < 2854) {
        this.statisticsColors.WOTRatingColor  = {
          color: '#FE0E01'
        };
      } else if (value >= 2854 && value <= 4810) {
        this.statisticsColors.WOTRatingColor  = {
          color: '#FF7D00'
        };
      } else if (value >= 4810 && value <= 6905) {
        this.statisticsColors.WOTRatingColor  = {
          color: '#FDF109'
        };
      } else if (value >= 6905 && value <= 9161) {
        this.statisticsColors.WOTRatingColor  = {
          color: '#5BFF00'
        };
      } else if (value >= 9161 && value <= 10606) {
        this.statisticsColors.WOTRatingColor = {
          color: '#00CAB3'
        };
      } else {
        this.statisticsColors.WOTRatingColor  = {
          color: '#D93DF9'
        };
      } 
    },
    victories() {
      if (this.victories < 46.4) {
        this.statisticsColors.victoriesColor  = {
          color: '#FE0E01'
        };
      } else if (this.victories >= 46.4 && this.victories <= 49.2) {
        this.statisticsColors.victoriesColor  = {
          color: '#FF7D00'
        };
      } else if (this.victories >= 49.2 && this.victories <= 52.6) {
        this.statisticsColors.victoriesColor  = {
          color: '#FDF109'
        };
      } else if (this.victories >= 52.6 && this.victories <= 57.8) {
        this.statisticsColors.victoriesColor  = {
          color: '#5BFF00'
        };
      } else if (this.victories >= 57.8 && this.victories <= 63.5) {
        this.statisticsColors.victoriesColor = {
          color: '#00CAB3'
        };
      } else {
        this.statisticsColors.victoriesColor  = {
          color: '#D93DF9'
        }
      }
    }
  }
}




