import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    playerData: null
  },
  mutations: {
    SET_PLAYER_DATA: (state, payload) => {
      state.playerData = payload;
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    PLAYER_DATA: (state) => {
      return state.playerData;
    }
  }
})
